<template>
  <layout-default>
    <div class="page-content">
      <div class="container-fluid">

          <!-- start page title -->
          <div class="row">
              <div class="col-12">
                  <div class="page-title-box d-flex align-items-center justify-content-between">
                      <h4 class="mb-0">معلومات الموظف</h4>
                      <div class="page-title-right">
                          <form-modal ref="FormModal" :fetchData="fetchData"/>
                      </div>

                  </div>
              </div>
          </div>
          <!-- end page title -->
          
          <div class="row mb-4" >
            <div class="col-12" v-if="user">
                <div class="card h-100">
                    <div class="card-body" style="min-height: 500px;">
                        <div class="text-center">
                            <div class="dropdown float-end">
                                <a class="text-body dropdown-toggle font-size-18" href="#" role="button" data-bs-toggle="dropdown" aria-haspopup="true">
                                    <i class="uil uil-ellipsis-v"></i>
                                </a>
                                
                                <div class="dropdown-menu dropdown-menu-end">
                                    <a @click="$refs.FormModal.edit(user)" class="dropdown-item" href="javascript:;">
                                      <i class="far fa-edit"></i> تعديل البيانات
                                    </a>
                                </div>
                            </div>
                            <div class="clearfix"></div>
                        </div>

                        <div class="text-muted">
                            <div class="mt-4">
                                <div class="row ">
                                    <p class="mt-4 mb-1 col-md-3 font-size-15"><b style="float: left;"> الأسم : </b>	&nbsp; {{user.name}} </p>
                                    <p class="mt-4 mb-1 col-md-3 font-size-15"><b style="float: left;"> التليفون : </b>	&nbsp; {{user.phone}} </p>
                                    <p class="mt-4 mb-1 col-md-3 font-size-15"><b style="float: left;"> الوظيفة : </b>	&nbsp; {{user.role}} </p>
                                    <p class="mt-4 mb-1 col-md-3 font-size-15"><b style="float: left;"> الفرع : </b>	&nbsp; {{(user.store)?user.store.name:'-----'}} </p>
                                    <p class="mt-4 mb-1 col-md-3 font-size-15"><b style="float: left;"> الخزينة : </b>	&nbsp; {{moneyFormat(user.money,0)}} EGP</p>
                                    
                                    <p class="mt-4 mb-1 col-md-3 font-size-15" >
                                      <b style="float: left;"> تاريخ التعيين : </b>
                                    	&nbsp; {{user.created_at| moment("DD-MM-YYYY")}}
                                    </p>
                                    
                                </div>
                            </div>
                        </div>
                        <h5 class="font-size-20 mb-4 mt-5 text-center" style="font-weight:600;">تعاملات الموظف ({{moneyFormat(total,0)}} EGP)</h5>
                        <div class="row">
                          <div class="col-md-3">
                              <Datepicker @input="reports()" placeholder="Select Date" input-class="form-control" v-model="filter.date" format="dd-MM-yyyy"></Datepicker>
                          </div>
                          <div class="col-md-3">
                              <a @click="clear" class="btn btn-primary" href="javascript:;">اليوم</a>
                          </div>
                        </div>
                        <table  v-if="rows&&rows.length" class="mt-3 table table-bordered dt-responsive nowrap" style="border-collapse: collapse; border-spacing: 0; width: 100%;">
                          <thead>
                              <tr>
                                  <th>ملاحظات</th>
                                  <th>البيان</th>
                                  <th>النوع</th>
                                  <th>المبلغ</th>
                                  <th>التاريخ</th>
                              </tr>
                          </thead>
                          <tbody>
                              <tr v-for="(row,index) in rows" >
                                  <td>{{row.note1}} </td>
                                  <td>{{row.note}} </td>
                                  <td>{{row.type}} </td>
                                  <td v-if="row.status==1" class="text-success" style="font-weight:bold;">
                                        <i class="fas fa-arrow-down"></i> {{moneyFormat(row.money,0)}}
                                  </td>
                                  <td v-else class="text-danger" style="font-weight:bold;">
                                        <i class="fas fa-arrow-up"></i> {{moneyFormat(row.money,0)}}
                                  </td>
                                  <td>{{row.created_at| moment("DD-MM-YYYY")}}</td>
                              </tr>
                          </tbody>
                        </table>
                        <div v-else class="mt-3 row">
                          <beat-loader v-if="loader" :loading="loader" color="#5578eb"></beat-loader>

                          <p v-else class="card-title-desc" style="text-align: center;">
                            لا يوجد بيانات حتى في هذا اليوم
                          </p>
                        </div>
                    </div>
                </div>
                
            </div>
        </div>
      </div> <!-- container-fluid -->
    </div>
  </layout-default>
</template>  


<script>
import LayoutDefault from '../../layouts/App';
import PaginationNav from '../../components/pagination';
import FormModal from '../../components/modals/user/add';
import Datepicker from 'vuejs-datepicker';
import axios from 'axios';

export default {
  components: {LayoutDefault,PaginationNav,FormModal,Datepicker},
  data() {
      return {
        user:null,loader:false,
        data: null,
        rows: null,
        total: 0,
        filter:{
          user_id:null,
          date:new Date().toLocaleString(),
        }
      
      }
    },  
  created() {
    this.fetchData();
  },
  methods: {
    clear() {
        this.filter.date=new Date().toLocaleString()
        this.reports()
    },
    fetchData() {
      this.loader=true;
      axios.get('/api/users/'+this.$route.params.user_id).then(({data}) => {
          this.user=data.success.user;
          this.filter.user_id=this.$route.params.user_id
          this.reports()
      });  
    },
    reports() {
      this.loader=true;
      let data = {params: {filter:this.filter}};
      axios.get('/api/reports/user', data).then(({ data }) => {
        // console.log(data.success);
        this.rows=data.success;
        let that=this
        that.total=0;
        data.success.forEach(function (value) {
          if(value.status==1){
            that.total+=value.money/1
          }else{
            that.total-=value.money/1
          }
        });


        this.loader=false;
      });  
    },
  },
}
</script>
<style scoped>

</style>